import Loading from "../components/Loading";
export default {
  install: (Vue) => {
    Vue.component("Loading", Loading);
    let VueLoading = Vue.extend(Loading);
    let loading = new VueLoading();
    document.querySelector("body").appendChild(loading.$mount().$el);
    let $loading = {};

    $loading.show = () => {
      loading.show();
    };
    $loading.hide = () => {
      loading.hide();
    };
    Vue.prototype.$loading = $loading;
    Object.defineProperty(Vue.prototype, "$loading", {
      writable: false,
    });
  },
};
